                    <th style="width:7%;background-color:#00d9ff;text-align:center;" rowspan="1" colspan="1">Well ID
                    </th>
                    <th style="width:20%;background-color:#00d9ff;text-align:center;" rowspan="1" colspan="1">VSD Brand
                    </th>
