                    </th>
                    <th style="width:20%;background-color:#00d9ff;text-align:center;" rowspan="1" colspan="1">Pump Model
                    </th>
                    <th style="width:10%;background-color:#00d9ff;text-align:center;" rowspan="1" colspan="1">Run Status
                    </th>
                    <th style="width:20%;background-color:#00d9ff;text-align:center;" rowspan="1" colspan="1">Connection
