<script>
import $ from "jquery";
export default {
    name: "Datatabel01",

    data() {
        return {
            data01: [],
            header01: [
                {
                    targets: 0, title: "No", render: function (data, type, row, meta) {
                        if (type === 'display') {
                            data = '<div class="text-center"><a style="">' + data + '</a></div>';
                        }
                        return data;
                    }
                },
                {
                    targets: 1, title: "Well ID", render: function (data, type, row, meta) {
                        if (type === 'display') {
                            data = '<div class="text-center"><a href="/welldetail01view/' + encodeURIComponent(data) + '">' + data + '</a></div>';
                        }
                        return data;
                    }
                },
                {
                    targets: 2, title: "VSD Brand", render: function (data, type, row, meta) {
                        if (type === 'display') {
                            data = '<div class="text-left"><a style="text-align:center;">' + data + '</a></div>';
                        }
                        return data;
                    }
                },
                {
                    targets: 3, title: "Drivehead Model", render: function (data, type, row, meta) {
                        if (type === 'display') {
                            data = '<div class="text-left"><a style="text-align:left;height:auto;">' + data + '</a></div>';
                        }
                        return data;
                    }
                },
                {
                    targets: 4, title: "Pump Model", render: function (data, type, row, meta) {
                        if (type === 'display') {
                            data = '<div class="text-left"><a style="text-align:left;">' + data + '</a></div>';
                        }
                        return data;
                    }
                },
                {
                    targets: 5, title: "Run Status", render: function (data, type, row, meta) {
                        if (type === 'display') {
                            if (data == "Running") { var colour = "green" }
                            if (data == "Stop") { var colour = "red" }
                            if (data == "Standby") { var colour = "orange" }
                            var data_mod = '<i class="fa fa-circle" aria-hidden="true" style="color:' + colour + '"></i> ' + data
                            data = '<div class="text-left"><a style="text-align:center;">' + data_mod + '</a></div>';
                        }
                        return data;
                    }
                }, {
                    targets: 6, title: "Connection Status", render: function (data, type, row, meta) {
                        var dataarray = data.split(" - ")
                        if (type === 'display') {
                            if (dataarray[0] == "Connected") { var colour = "yellow" }
                            if (dataarray[0] == "Disconnected") { var colour = "red" }
                            // if (data == "Standby") { var colour = "orange" }
                            var data_mod = '<i class="fa fa-bolt my-bordered-icon" aria-hidden="true" style="border-color: red;color:' + colour + '"></i> ' + dataarray[0] + "<br>" + dataarray[1]
                            data = '<div class="text-left"><a style="text-align:center;">' + data_mod + '</a></div>';
                        }
                        return data;
                    }
                }],
            label01: [{ data: "no" }, { data: "well_id" }, { data: "vsd_brand" }, { data: "drivehead_model" }, { data: "pump_model" }, { data: "run_status" }, { data: "connection_status" }],
        };
    },

